/*Слайдер*/

#i1, #i2, #i3, #i4, #i5{ display: none;}

.top-slider{
    margin: 0 auto;
    margin-top: 0;
    position: relative;
    width: 100%;
    height: 372px;
    padding-bottom: 38%;
    user-select: none;
    background-color: #747f91;
  }

  .top-slider .slide_top{
    position: absolute;
    width: 100%;;
    min-height: 372px;
  }
  .top-slider .slide_top img{
    width: inherit;
    height: inherit;
  }

  .prev, .next{
    width: 12%;
    height: inherit;
    position: absolute;
    top:0;
    background-color: rgba(88, 88, 88,.2);
    color:rgba(244, 244, 244,.9);
    z-index: 99;
    transition: .45s;
    cursor: pointer;
    text-align: center;

    @media screen and (max-width: 685px){
      display: none;
    }
  }

  .next{right:0;}
  .prev{left:0;}

  label span{
    position: absolute;
    font-size: 100pt;
    top: 50%;
    transform: translateY(-50%);
  }

  .prev:hover, .next:hover{
    transition: .3s;
    background-color: rgba(88, 88, 89,.8);
    color: #ffffff;
  }

.top-slider #nav_slide{
  width: 100%;
  bottom: 12%;
  height: 11px;
  position: absolute;
  text-align: center;
  z-index: 99;
  cursor: default;
}

#nav_slide .dots{
  top: -5px;
  width: 18px;
  height: 18px;
  margin: 0 4px;
  position: relative;
  border-radius: 100%;
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.4);
  transition: .4s;
}

#nav_slide .dots:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.9);
  transition: .25s
}

.slide_top{z-index: -1;}

  #i1:checked ~ #one  ,
  #i2:checked ~ #two  ,
  #i3:checked ~ #three,
  #i4:checked ~ #four ,
  #i5:checked ~ #five
  {z-index: 9; animation: scroll 1s ease-in-out;}

  #i1:checked  ~  #nav_slide #dot1,
  #i2:checked  ~  #nav_slide #dot2,
  #i3:checked  ~  #nav_slide #dot3,
  #i4:checked  ~  #nav_slide #dot4,
  #i5:checked  ~  #nav_slide #dot5
  { background-color: rgba(225, 225, 225, 1);}

@keyframes scroll{
  0%{ opacity:.4;}
  100%{opacity:1;}
}

