@media screen and (max-width: 685px){
  .top-slider{
    border: none;
    width: 100%;

    padding-bottom: 0;
  }

  label span { font-size: 50pt; }

  .prev, .next{
    width: 15%;
  }
  #nav_slide .dots{
    width: 12px;
    height: 12px;
  }
}
@media screen  and(min-width: 970px){
  .me{ display: none;}
}

